import React from 'react'
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'
import bellCurve from 'highcharts/modules/histogram-bellcurve';
bellCurve(Highcharts);

let GraphData = {
    chart: {
        type: 'column'
    },
    title: {
        text: 'Users Lifetime'
    },
    xAxis: {
        type: 'category',
        labels: {
            rotation: -45,
            style: {
                fontSize: '16px',
                fontFamily: 'Ubuntu, sans-serif'
            }
        }
    },
    yAxis: {
        min: 0,
        title: {
            text: 'Lifetime (days)',
            fontSize: '100px'
        }
    },
    legend: {
        enabled: false
    },
    series: [{
        type: 'histogram',
        name: 'Days',
        data: [],
        dataLabels: {
            enabled: true,
            rotation: -90,
            color: '#FFFFFF',
            align: 'right',
            format: '{point.y:.1f}',
            y: 10, 
            style: {
                fontSize: '16px',
                fontFamily: 'Ubuntu, sans-serif'
            }
        }
    }]
};

export default class Graph extends React.Component {
    
    componentDidMount() {
        GraphData.series[0].data = this.props.data
    }

    render() {
        return (
            <HighchartsReact 
                highcharts={Highcharts}
                options={GraphData}
                updateArgs={[true]}
            />
        )
    }
} 