import React from 'react'

export default class Table extends React.Component {
    render() {
        return (
                <table className="table">
                    <thead>
                        <tr>
                            <th>User ID</th>
                            <th>Registration</th>
                            <th>Last Activity</th>
                        </tr>
                    </thead>

                    <tbody>{
                        this.props.data.map(activity => {
                            return <Row activity={activity} onRemove={this.props.onRemove} key={activity.userID} />
                                // this.props.isEdit ? 
                                // <RowEdit activity={activity} onRemove={this.props.onRemove} key={this.props.activity.userID} /> :
                                // <Row activity={activity} onRemove={this.props.onRemove} />
                        })
                    }
                    </tbody>
                </table>
        )
    }
}

class Row extends React.Component {
    render() {
        return (
            <tr>
                <td>{this.props.activity.userID}</td>
                <td>{this.props.activity.dateRegistration}</td>
                <td>{this.props.activity.dateLastActivity}</td>
                <td>
                    <button className="edit-btn" onClick={this.props.onChangeMode}>EditMode</button>
                    <button className="remove-btn" onClick={() => this.props.onRemove(this.props.activity.userID)}>Remove</button>
                </td>
            </tr>
        )
    }
}

