import React from 'react';
import Table from './Table.js'
import Graph from './Graph.js'

export default class Field extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            userID: '', 
            dateRegistration: '', 
            dateLastActivity: '',
            activities: [],
            parameters: [],
            result: '',
            isEdit: false
        };

        this.onChangeID = this.onChangeID.bind(this)
        this.onChangeReg = this.onChangeReg.bind(this)
        this.onChangeLast = this.onChangeLast.bind(this)
        this.onChangeMode = this.onChangeMode.bind(this)
        this.onRemoveUser = this.onRemoveUser.bind(this)
    }

    onChangeID = e => this.setState({userID: Math.round(e.target.value, 0)})
    onChangeReg = e => this.setState({dateRegistration: e.target.value})
    onChangeLast = e => this.setState({dateLastActivity: e.target.value})
    onChangeMode = () => this.setState({isEdit: this.state.isEdit ? false : true})

    onRemoveUser = async (userID) => {
        const requestOptions = {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json' }
        };

        try {
            const response = await fetch(`https://mainapi-react.appspot.com/main/removeone/${userID}`, requestOptions);

            if (response.ok) 
            {
                this.state.activities.forEach((activity, index) => { if (activity.userID === userID) this.state.activities.splice(index, 1)} )
                this.setState({userID: ''})
            }
            else {
                const data = await response.json();
                try {
                    alert(JSON.stringify(data.errors))
                }
                catch {
                    alert(data)
                }
            }
        }
        catch (ex) {
            console.log(ex)
            alert("Отсутствует соединение с сервером")
        }
    }

    async componentDidMount() {
        const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' }
        };

        try {
            const response = await fetch('https://mainapi-react.appspot.com/main/all', requestOptions);
            const data = await response.json();

            if (response.ok) 
            {
                this.setState({activities: data})
            }
            else {
                try {
                    alert(JSON.stringify(data.errors))
                }
                catch {
                    alert(data)
                }
            }
        }
        catch (ex) {
            alert("Отсутствует соединение с сервером")
        }
    }

    onCalc = async () => {
        if (this.state.activities.length < 1) return;

        const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        };

        try {
            const response = await fetch('https://mainapi-react.appspot.com/main/retention', requestOptions);
            const data = await response.json(); 

            if (response.ok) 
            {
                data.data.forEach(group => {
                    this.state.parameters.push([group.usersCount, group.days])
                });

                this.state.parameters.sort(function(x, y) {
                        if (x[0] > y[0]) {
                          return 1;
                        }
                        if (x[0] < y[0]) {
                          return -1;
                        }
                        return 0;
                      });

                this.setState({result: `Rolling Retention 7 day: ${data.result} %`})
            }
            else {
                try {
                    alert(JSON.stringify(data.errors))
                }
                catch {
                    alert(data)
                }
            }
        }
        catch (ex) {
            alert("Отсутствует соединение с сервером")
        }

        document.getElementById("graph").className = "graph"
    }

    onSave = async () => {
        if (parseInt(this.state.userID) < 0 || this.state.userID.length < 1) {
            alert("Некорректный идентификатор")
            return;
        }

        if (this.state.dateRegistration.length < 1 || this.state.dateLastActivity.length < 1) {
            alert("Некорректная дата")
            return;
        }
          
        var same = this.state.activities.filter(activity => activity.userID === this.state.userID)

        if (same.length > 0) {
            alert("Пользователь с таким идентификатором уже существует")
            return;
        }

        if (new Date(this.state.dateRegistration).getTime() > new Date(this.state.dateLastActivity).getTime()){
            alert("Дата регистрации должна быть раньше даты последней активности")
            return;
        }

        if (new Date().getTime() < new Date(this.state.dateRegistration).getTime || new Date().getTime() < new Date(this.state.dateLastActivity).getTime()) {
            alert("Дата не может быть больше текущей")
            return;
        }

        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                userID: this.state.userID,
                dateRegistration: this.state.dateRegistration,
                dateLastActivity: this.state.dateLastActivity
            })
        };

        try {

            const response = await fetch('https://mainapi-react.appspot.com/main/saveone', requestOptions)

            if (response.ok) 
            {
                this.state.activities.push(await response.json())
                this.setState({ userid: '' })
            }
            else 
                console.error(response) 
        } 
        catch (ex) {
            console.error(ex)
            alert(ex)
        }

        this.setState({userID: '', dateRegistration: '', dateLastActivity: ''})
    }

    render() {
        return (
            <div className="field">

                <div className="in">
                    <h1 className="field-title">Activities</h1>

                    <div className="input-place">
                        <input className="input-id" type="number" value={this.state.userID} onChange={this.onChangeID} placeholder = "UserID" />
                        <input className="input-reg" type="date" value={this.state.dateRegistration} onChange={this.onChangeReg} />
                        <input className="input-reg" type="date" value={this.state.dateLastActivity} onChange={this.onChangeLast} />
                        <button type="submit" className="input-btn" onClick={this.onSave}>Save</button>
                    </div>

                    <Table 
                        data={this.state.activities} 
                        onChangeMode={this.onChangeMode} 
                        isEdit={this.state.isEdit} 
                        onRemove={this.onRemoveUser}
                    />
                    
                    <div className="calculate-place">
                        <h3 className="retention">{this.state.result}</h3>
                        <button type="submit" className="calculate" onClick={this.onCalc}>Calculate</button>
                    </div>
                    
                    <div id="graph" className="graph-none">
                        <Graph data={this.state.parameters} />
                    </div>

                </div>  
            </div>
        )
    }
}     